// Module
___CSS_LOADER_EXPORT___.push([module.id, `@media screen and (max-width: 700px) {
  #drawer ul li,
  #hamburger {
    display: block;
  }
  #drawer > ul {
    margin-top: 4px;
    padding-top: 4px;
    border-top: 1px solid #333333;
    flex-direction: column;
  }
  #drawer ul li {
    margin-bottom: 4px;
  }
  #drawer > ul > li > a {
    display: block;
    padding: 20px;
    text-decoration: none;
    color: #333;
    border-radius: 0;
    border-bottom: 1px solid #333333;
    transition: background-color 0.3s;
  }
  #drawer > ul > li > a:hover {
    background-color: #f0f0f0;
  }
  #drawer {
    width: 300px;
    overflow-y: auto;
    position: fixed;
    top: 115px;
    left: -300px;
    z-index: 999;
    transition: left 0.3s ease-in-out;
    background-color: #fff;
    color: #000;
  }
  #drawer.open {
    left: 0;
  }

  .quiz-container,
  .quiz-card-container {
    grid-template-columns: 1fr;
    grid-auto-rows: auto;
    grid-auto-flow: row;
  }

  .label-quiz,
  .data-quiz {
    padding: 0 20px;
  }

  .input-form {
    min-width: 500px;
  }
}
@media screen and (max-width: 500px) {
  .input-form {
    min-width: 400px;
  }

  footer {
    font-size: 12px;
  }
}
@media screen and (max-width: 400px) {
  .header-container {
    flex-direction: column;
  }
  #drawer {
    top: 203px;
  }

  #hamburger {
    margin-right: 0;
  }

  .input-form {
    min-width: 300px;
  }

  .heading-content {
    font-size: 24px;
  }
}
